import { createApp } from 'vue';
import App from './App.vue'; // 确保引入 App.vue
import router from './router';
import ElementPlus from 'element-plus';
import 'element-plus/dist/index.css'; // 引入 Element Plus 样式

// 添加 Font Awesome
import { library } from '@fortawesome/fontawesome-svg-core'
import { faWeixin } from '@fortawesome/free-brands-svg-icons';
import { faPeopleGroup, faCartFlatbed, faHandHoldingHeart, faPhone, faPhoneVolume, faAngleUp} from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faPeopleGroup,faCartFlatbed,faHandHoldingHeart,faPhone,faPhoneVolume,faWeixin,faAngleUp)

const app = createApp(App); // 使用 App 作为根组件
app.component('font-awesome-icon', FontAwesomeIcon)
app.use(ElementPlus);
app.use(router);
app.mount('#app'); // 确保挂载到正确的元素